/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
  height: 100%;
}

body {
  min-height: 100%;
  display: flex;
  flex-direction: column;
  background-color: #1a1b15;
  color: #FFFFFF;
  font-family: "Poppins", sans-serif;
  font-size: 1.6rem;
}
@media (min-width: 768px) {
  body {
    height: 100%;
  }
}

*, *:before, *:after {
  box-sizing: inherit;
}

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

.contenedor-small {
  width: 40%;
  max-width: 1200px;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

.enlace {
  color: #077d9a;
}
.enlace:hover {
  color: #09a5ca;
}

img {
  max-width: 100%;
}

.mostrar {
  display: block;
}

.ocultar {
  display: none;
}

h1, h2, h3 {
  font-family: "Inter", sans-serif;
  margin: 0 0 2.5rem 0;
  font-weight: 900;
}

h1 {
  font-size: 6rem;
}

h2 {
  font-size: 5rem;
}

h3 {
  font-size: 4rem;
}

p {
  font-size: 2rem;
  color: #6b7280;
  line-height: 1.8;
}

.nombre-pagina {
  text-align: center;
  margin: 5rem 0;
  font-weight: 700;
}

.descripcion-pagina {
  text-align: center;
}

.nombre {
  display: flex;
  justify-content: space-between;
  text-align: center;
  align-items: center;
}
.nombre p {
  color: #FFFFFF;
}
.nombre p span {
  color: #5484ef;
}
.nombre a {
  background-color: #2563EB;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  font-size: 1.6rem;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
  border-radius: 0.5rem;
  width: 50%;
}
@media (min-width: 768px) {
  .nombre a {
    width: auto;
  }
}
.nombre a:hover {
  background-color: #134cca;
  cursor: pointer;
}
@media (min-width: 768px) {
  .nombre a {
    padding: 1rem 2rem;
  }
}

@media (min-width: 768px) {
  .contenedor-app {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    min-height: 100vh;
  }
}
@media (min-width: 768px) and (min-width: 768px) {
  .contenedor-app {
    overflow: scroll;
  }
}
.contenedor-app .imagen {
  height: 20rem;
  background-image: url(../img/1.png);
  background-size: cover;
  background-position: center center;
}
@media (min-width: 768px) {
  .contenedor-app .imagen {
    height: auto;
  }
}
.contenedor-app .app {
  width: 95%;
  margin: 0 auto;
  padding: 3rem 0;
}
@media (min-width: 768px) {
  .contenedor-app .app {
    overflow: scroll;
  }
}

.nav {
  display: flex;
  justify-content: space-between;
}

.app::-webkit-scrollbar {
  display: none;
}

@media (min-width: 768px) {
  .tabs {
    display: flex;
  }
}
.tabs button {
  background-color: #2563EB;
  border: none;
  color: #FFFFFF;
  padding: 1rem;
  display: block;
  width: 100%;
  margin-bottom: 1rem;
  text-transform: uppercase;
  font-weight: 700;
  transition: background-color 300ms ease;
}
.tabs button.actual {
  background-color: #FFFFFF;
  color: #2563EB;
}
.tabs button:hover {
  cursor: pointer;
  background-color: #134cca;
}
@media (min-width: 768px) {
  .tabs button:first-of-type {
    border-top-left-radius: 1rem;
    border-bottom-left-radius: 1rem;
  }
  .tabs button:last-of-type {
    border-top-right-radius: 1rem;
    border-bottom-right-radius: 1rem;
  }
}

.direcciones {
  background-color: #090a0c;
  border-radius: 1rem;
  border: 1px solid #FFFFFF;
  transition-property: background-color padding;
  transition-duration: 0.5s;
}
.direcciones h3 {
  text-align: center;
  color: #FFFFFF;
}
.direcciones p {
  margin-left: 1rem;
  color: #FFFFFF;
}
.direcciones p span {
  color: #2563EB;
}
.direcciones:hover {
  cursor: pointer;
  padding: 0 1rem 0;
  background-color: #14171a;
}

.formulario {
  width: 100%;
  margin-top: 5rem;
}

.campo {
  display: flex;
  margin-bottom: 2rem;
  align-items: center;
}
.campo label,
.campo button {
  flex: 0 0 10rem;
}
.campo input,
.campo select {
  flex: 1;
  border: none;
  padding: 1.25rem;
  border-radius: 1rem;
}
.campo input:disabled,
.campo select:disabled {
  background-color: #6d7158;
  color: #FFFFFF;
  cursor: not-allowed;
}
.campo option {
  color: #1a1b15;
}

.boton-azul {
  background-color: #2563EB;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  font-size: 1.6rem;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
  border-radius: 0.5rem;
}
@media (min-width: 768px) {
  .boton-azul {
    width: auto;
  }
}
.boton-azul:hover {
  background-color: #134cca;
  cursor: pointer;
}

.boton-eliminar {
  background-color: #a90000;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  font-size: 1.6rem;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
  border-radius: 0.5rem;
}
@media (min-width: 768px) {
  .boton-eliminar {
    width: auto;
  }
}
.boton-eliminar:hover {
  background-color: #760000;
  cursor: pointer;
}

.boton-verde {
  background-color: #007800;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  font-size: 1.6rem;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
  border-radius: 0.5rem;
}
@media (min-width: 768px) {
  .boton-verde {
    width: auto;
  }
}
.boton-verde:hover {
  background-color: #004500;
  cursor: pointer;
}

.boton-verde-flex {
  margin-bottom: 2rem;
  background-color: #007800;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  font-size: 1.6rem;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  width: 45%;
  border-radius: 0.5rem;
}
@media (min-width: 768px) {
  .boton-verde-flex {
    width: auto;
  }
}
.boton-verde-flex:hover {
  background-color: #004500;
  cursor: pointer;
}

@media (min-width: 768px) {
  .acciones {
    display: flex;
    justify-content: space-between;
  }
}
.acciones a {
  display: block;
  padding-top: 2.5rem;
  text-align: center;
  color: #FFFFFF;
}
.acciones a:hover {
  color: #b3b3b3;
}

.alerta {
  margin-top: 1rem;
  margin-bottom: 1rem;
  border-radius: 1rem;
  padding: 1rem 0;
  text-align: center;
  text-transform: uppercase;
  font-weight: 700;
}
.alerta.error {
  background-color: #a90000;
}
.alerta.exito {
  background-color: #007800;
}

.paginacion {
  display: flex;
  justify-content: space-between;
}
.paginacion button {
  margin: 0 0 1rem 0;
}

.ocultar {
  visibility: hidden;
}

.buscador {
  margin-left: 0.5rem;
}