@use 'base/variables' as v;

.alerta{

    margin-top: 1rem;
    margin-bottom: 1rem;
    border-radius: 1rem;
    padding: 1rem 0;
    text-align: center;
    text-transform: uppercase;
    font-weight: v.$bold;


    &.error{
        background-color: v.$rojo;
    }

    &.exito{
        background-color: v.$verde;
    }
}