@use 'base/variables' as v;
@use 'base/mixins' as m;

.contenedor-app{
    @include m.tablet{
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        min-height: 100vh;
        @include m.tablet{
            overflow: scroll;
        }
    }

    .imagen{
        height: 20rem;
        background-image: url(../img/1.png);
        background-size: cover;
        background-position: center center;
        @include m.tablet {
            height: auto;
        }
    }

    .app{
        width: 95%;
        margin: 0 auto;
        padding: 3rem 0;
        @include m.tablet{
            overflow: scroll;
        }
    }
}
.nav{
    display: flex;
    justify-content: space-between;
}
.app::-webkit-scrollbar {
    display: none;
}
