@use 'base/variables' as v;
@use 'base/mixins' as m;

.direcciones{
    background-color: darken(v.$grisOscuro, 30%);
    border-radius: 1rem;
    border: 1px solid v.$blanco;
    transition-property: background-color padding;
    transition-duration: .5s;

    h3{
        text-align: center;
        color: v.$blanco;
    }

    p{
        margin-left: 1rem;
        color: v.$blanco;

        span{
            color: v.$azul;
        }
    }

    &:hover{
        cursor: pointer;
        padding: 0 1rem 0 ;
        background-color: darken(v.$grisOscuro, 25%);
    }
}