@use 'base/variables' as v;
@use 'base/mixins' as m;

.boton-azul {
    @include m.boton(v.$azul, v.$blanco);
    border-radius: .5rem;
}
.boton-eliminar{
    @include m.boton(v.$rojo, v.$blanco);
    border-radius: .5rem;
}
.boton-verde{
    @include m.boton(v.$verde, v.$blanco);
    border-radius: .5rem;
}
.boton-verde-flex{
    margin-bottom: 2rem;
    background-color: v.$verde;
    padding: 1.5rem 4rem;
    color: v.$blanco;
    font-size: 1.6rem;
    font-weight: 700;
    border: none;
    transition-property: background-color;
    transition-duration: .3s;
    text-align: center;
    width: 45%;
    border-radius: .5rem;
    @include m.tablet {
        width: auto;
    }
    &:hover {
        background-color: darken( v.$verde, 10% );
        cursor: pointer;
    }
}