@use 'base/variables' as v;
@use 'base/mixins' as m;

.acciones{

    @include m.tablet{
        display: flex;
        justify-content: space-between;
    }

    a{
        display: block;
        padding-top: 2.5rem;
        text-align: center;
        color: v.$blanco;

        &:hover {
            color: darken(v.$blanco, 30%);
        }

        
    }
}