@use 'base/variables' as v;


.formulario {
    width: 100%;
    margin-top: 5rem;
}
.campo{
    display: flex;
    margin-bottom: 2rem;
    align-items: center;
    label,
    button{
        flex: 0 0 10rem;
    }
    input,
    select {
        flex: 1;
        border: none;
        padding: calc(v.$separacion / 4);
        border-radius: 1rem;

        &:disabled {
            background-color: lighten(v.$negro, 30%);
            color:  v.$blanco;
            cursor: not-allowed;
        }
    }
    option{
        color: v.$negro;
    }
}