@use 'variables' as v;
@use 'mixins' as m;

html {
    font-size: 62.5%;
    box-sizing: border-box;
    height: 100%;
}
body {
    min-height: 100%;
    @include m.tablet{
        height: 100%;
    }
    display: flex;
    flex-direction: column;
    background-color: v.$negro;
    color: v.$blanco;
    font-family: v.$fuente_principal;
    font-size: 1.6rem;
}
*, *:before, *:after {
    box-sizing: inherit;
}
.contenedor {
    width: 95%;
    max-width: 1200px;
    margin: 0 auto;
}
.contenedor-small{
    width: 40%;
    max-width: 1200px;
    margin: 0 auto;
}
a {
    text-decoration: none;
}
.enlace{
    color: darken(v.$cyan, 5%);

    &:hover{
        color: lighten(v.$cyan, 5%);
    }
}
img {
    max-width: 100%;
}

.mostrar{
    display: block;
}
.ocultar{
    display: none;
}
