@use 'variables' as v;
@use 'mixins' as m;

h1, h2, h3 {
    font-family: v.$fuente_headings;
    margin: 0 0 calc(v.$separacion / 2) 0 ;
    font-weight: 900;
}
h1 {
    font-size: 6rem;
}
h2 {
    font-size: 5rem;
}
h3 {
    font-size: 4rem;
}
p {
    font-size: 2rem;
    color: v.$gris;
    line-height: 1.8;
}
.nombre-pagina{
    text-align: center;
    margin: 5rem 0;
    font-weight: v.$bold;
}
.descripcion-pagina{
    text-align: center;
}

.nombre{
    display: flex;
    justify-content: space-between;
    text-align: center;
    align-items: center;

    p{
        color: v.$blanco;

        span{
            color: lighten(v.$azul, 10%);
        }
    }

    a{
        @include m.boton(v.$azul, v.$blanco);
        border-radius: .5rem;
        width: 50%;
        @include m.tablet{
            padding: 1rem 2rem;
        }
    }
}